.add-note {
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
  margin: 1rem 0 1.5rem 0;
  width: 15rem;
}

.icon.icon-plus {
  color: rgba(233, 229, 244, 0.9);
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  transition: all 0.2s;
}

.icon.icon-plus:hover {
  filter: brightness(1.2);
  color: rgba(109, 115, 154, 0.735);
}

@media screen and (max-width: 650px) {
  .add-note {
    width: 100%;
    margin-top: 0;
  }
}
